export const getItems: AppThunk = () => {
  return function (dispatch: AppDispatch) {
    fetch(`${baseUrl}/ingredients`)
      .then((res) => {
        dispatch({
          type: GET_ITEMS_REQUEST,
        });

        return res;
      })
      .then(checkResponse)
      .then((res) => {
        if (res && res.success) {
          dispatch({
            type: GET_ITEMS_SUCCESS,
            items: res.data,
          });
        } else {
          dispatch({
            type: GET_ITEMS_FAILED,
          });
        }
      })
      .catch((err) => {
        console.log(err); //    
      });
  };
};

export const updateCartList: AppThunk = (item: IIngredient) => {
  return function (dispatch: AppDispatch) {
    if (item.type !== "bun") {
      dispatch({
        type: ADD_TO_CART_FILLING,
        item,
      });
      dispatch({
        type: INCREASE_FILLINGS_COUNTER,
        item,
      });
    } else {
      dispatch({
        type: DELETE_FROM_CART_BUN,
      });
      dispatch({
        type: ADD_TO_CART_BUN,
        item,
      });
    }
  };
};
